body {
  position: absolute;
  width: 50vw;
  height: 50vh;
  left: 19vw;
  top: 21vh;
}

#length-wrapper {
  display: inline-flex;
  width: 62vw;
}

#session {
  background-color: white;
  font-size: 5vh;
  width: 31vw;
  height: 17.5vh;
  padding: 0px;
  padding-top: 5vh;
  position: relative;
  text-align: center;
  border-right: 2px solid black;
  border-bottom: 4px solid black;
}

#break {
  background-color: white;
  font-size: 5vh;
  width: 31vw;
  height: 17.5vh;
  padding: 0px;
  padding-top: 5vh;
  position: relative;
  text-align: center;
  border-left: 2px solid black;
  border-bottom: 4px solid black;
}

#length-controls {
  display: inline-flex;
  background-color: goldenrod;
  width: 62vw;
  height: 7.5vh;
  padding-top: 0.5vh;
  position: absolute;
}

#break-decrement, #break-increment, #session-decrement, #session-increment {
  padding: 0px;
  margin: 0 0;
  width: 15.5vw;
  height: 7.5vh;
  font-size: 2.8vh;
}

#timer {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: black;
  color: white;
  margin-left: 15.5vw;
  margin-top: 0vh;
  width: 31vw;
  height: 20vh;
  padding-top: 2.5vh;
}

#timer-label {
  text-align: center;
  font-size: 5vh;
}

#time-left {
  text-align: center;
  font-size: 8vh;
}

#other-controls {
  height: 8vh;
  display: inline-flex;
  margin-left: 15.5vw;
  margin-top: 0;
}

#start_stop, #reset {
  width: 15.5vw;
  height: 8vh;
  color: white;
  border-radius: 3px;
  font-size: 4vh;
  border: solid 2px black;
}

#start_stop {
  background-color: olive;
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 0px;
  border-right: solid 1px black;
}

#start_stop:hover {
  background-color: olivedrab;
}

#reset:hover {
  background-color: brown;
}

#reset {
  background-color: maroon;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  border-top-left-radius: 0px;
  border-left: solid 1px black;
}